<template>
	<div id="box" v-if='data.data'>
		<scroll class="box">
			<div class="scroll">
				<div class="top" v-for='item,index in data.data.topList' v-if='index<10' @click='go(item)'>
					<img :src="item.picUrl" alt="">
					<div class="font">
						<p v-for='item1,index1 in item.songList'>{{index1+1}} {{item1.songname}}-{{item1.singername}}</p>
					</div>
				</div>
			</div>
		</scroll>
	</div>
</template>
<script>
	import scroll from '@/components/scroll.vue';
	import axios from 'axios';
	export default{
		data(){
			return{
				data: {}
			}
		},
		methods: {
			go(item){
				console.log(item)
				axios.get('/apis/topList').then(data => {
					console.log(data.data);
					/*this.$router.push({
						path: '/toplist',
						name: 'toplist',
						component: resolve => require(['@/components/toplist.vue'],resolve),
						params: {
							img: item.picUrl,
							topname: item.topTitle,
							data: data.data
						}
					})*/


					this.$router.push({
						path: '/details',
						name: 'details',
						component: resolve => require(['@/components/details.vue'],resolve),
						params: {
							title: item.topTitle,
							imgurl: item.picUrl,
							data: data.data,
							show: 2
						}
					})


				})
			}
		},
		mounted(){
			axios.get('/qq/v8/fcg-bin/fcg_myqq_toplist.fcg?_=1557730638305&g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1').then(data => {
				console.log(data.data);
				this.data = data.data;
			})
		},
		components: {
			scroll
		}
	}
</script>
<style scoped>
	#box{
		background: rgb(40,40,40);
		box-sizing: border-box;
		padding: 0 20px;
	}
	.box{
		height: 100vh;
		overflow: hidden;
	}
	.top{
		margin-top: 25px;
		background: rgb(53,53,53);
	}
	.top img{
		width: 110px;
		height: 110px;
		float: left;
		padding-right: 20px;
	}
	.font{
		box-sizing: border-box;
		padding-top: 5px;
		line-height: 35px;
	}
	.top p{
		color: rgb(112,112,112);
		font-size: 15px;
		overflow: hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
</style>


